.Wrapper {
  z-index: 0;
  &__movie {
    position: fixed;
    // z-index: ;
  }
}

.Top {
  width: 100%;
  height: 600px;
  background-image: linear-gradient(rgba(0,0,0,0.4),rgba(0,0,0,0.4));
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  background-attachment: fixed;
  justify-content: center;
  align-items: center;
  background-attachment: fixed;
  
  iframe{
    overflow: hidden;
    background-attachment: fixed;
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100vh;
  }
}

.logo {
  width: 270px;
  height: 270px;
  border-radius: 50%;
  background-color: #fff;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: center;
  align-items: center;
}
.logo img {
  width: 136px;
  height: 163px;
}


.Main {
  padding-top: 80px;
  padding-right: 16%;
  position: relative;
  background-color: #fff;
  background-color: #f4f4f4;
  &__pictures {
    list-style: none;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    position: relative;
    z-index: 0;
  }
  &__picture {
    width: 660px;
    height: 410px;
    background-color: #ccc;
    margin-bottom: 50px;
    &:last-child {
      margin-bottom: 111px;
    }
  }
  &__picture img {
    width: 100%;
    max-width: 100%;
    height: 410px;
    object-fit: cover;
  }
  &__logo {
      width: 110px;
      height: 133px;
      display: block;
      opacity: 0;
      position: fixed;
      top: 160px;
      left: 23%;
      transition: .2s;
  }
  &__logo img {
    transform: scale(2);
  }
}
