.Overlay {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
}

.Modal{
  display: none;
  
  &__block {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    z-index: 3;
  }
  
  &__back {
    position: fixed;
    right: 5rem;
    top: 2rem;
    appearance: none;
    border: none;
    cursor: pointer;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    user-select: none;
    width: 24px;
    background: transparent;
    outline: none;
    z-index: 3;
  }
  &__Close {
    &::before,
    &::after {
      position: absolute;
      content: "";
      display: block;
      width: 24px;
      height: 2px;
      background: #fff;
      border-radius: 2px;
    }
    &::before {
      transform: rotate(45deg);
    }
    &::after {
      transform: rotate(-45deg);
    }
  }
  &__main {
    height: 100%;
    color: #fff;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__stickername {
    font-size: 28px;
    margin-bottom: 53px;
  }
  &__intro {
    margin-left: 40px;
  }
  &__creator {
    font-size: 18px;
    margin-bottom: 20px;
  }
  &__members {
    font-size: 14px;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    margin-bottom: 26px;
  }
  &__member {
    margin-right: 60px;
  }
  &__profile {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    // justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  &__profile-img {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: #666;
    margin-bottom: 6px;
  }
  &__content {
    font-size: 16px;
    width: 500px;
    line-height: 30px;
  }
}